private const val link = "https://n8n-ext.onrender.com/webhook/e8e445ca-d9a2-425c-bf4e-b4ece8846cb6"
//private const val link = "?auth_date=0000000&query_id=00000000&user=00000000&hash=000000"

@OptIn(InternalAPI::class)
@Composable
fun fryInstructions() {
    Surface {
        Column(
            modifier = Modifier
                .fillMaxSize()
                .padding(top = 16.dp),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {

            Row {
                BlueButton(text = "RU", onClick = { })
                BlueButton(text = "ENG", onClick = { })
                BlueButton(text = "UA", onClick = { })
            }
            Spacer(Modifier.height(64.dp))
            Column {
                DropdownButton(
                    title = "Aspect Ratio",
                    leftIconRes = Res.drawable.main_aspect_ratio,
                    dropDownValues = persistentListOf(
                        "- - - - -",
                        "1:1", "5:4", "3:2", "3:2", "7:4",
                        "16:9", "2:1", "9:16", "1:2",
                    ),
                    onValueChanged = { },
                )
                DropdownButton(
                    title = "Styleflow",
                    leftIconRes = Res.drawable.main_styleflow,
                    dropDownValues = persistentListOf(
                        "- - - - -",
                        "raw"
                    ),
                    onValueChanged = { }
                )
            }

            val localScope = rememberCoroutineScope()
            BlueButton(
                text = "Test request",
                onClick = {
                    localScope.launch {
                        val client = HttpClient()
                        val response = client.get(link) {
                            url {
                                parameters.apply {
                                    append("auth_date", webApp.initDataUnsafe.authDate.toString())
                                    append("query_id", webApp.initDataUnsafe.queryId.toString())
                                    append("user", getUser())
                                    append("hash", webApp.initDataUnsafe.hash.toString())
                                }
                            }
                        }
                        window.alert(response.body<String>() + " , " + response.status + " , " + response.body<Any?>().toString())
                    }
                }
            )
        }
    }
}

private fun getUser(): String {
    val newUser = InitDataUserDto(
        id = webApp.initDataUnsafe.user?.id ?: -1,
        is_bot = webApp.initDataUnsafe.user?.isBot,
        first_name = webApp.initDataUnsafe.user?.firstName ?: "",
        last_name = webApp.initDataUnsafe.user?.lastName,
        username = webApp.initDataUnsafe.user?.username,
        language_code = webApp.initDataUnsafe.user?.languageCode,
        is_premium = webApp.initDataUnsafe.user?.isPremium,
        added_to_attachment_menu = webApp.initDataUnsafe.user?.addedToAttachmentMenu,
        allows_write_to_pm = webApp.initDataUnsafe.user?.allowsWriteToPm,
        photo_url = webApp.initDataUnsafe.user?.photoUrl
    )
    return Json.encodeToString(newUser)
}

@Serializable
data class InitDataUserDto(
    val id: Int,
    val is_bot: Boolean?,
    val first_name: String,
    val last_name: String?,
    val username: String?,
    val language_code: String?,
    val is_premium: Boolean?,
    val added_to_attachment_menu: Boolean?,
    val allows_write_to_pm: Boolean?,
    val photo_url: String?
)

fun getUserInitData(): String = StringBuilder()
    .append("")
    .toString()